import React, { Component } from 'react';
import Screen from './containers/Screen/Screen';
import Layout from './containers/Layout/Layout';
import StartScreen from './components/StartScreen/StartScreen';
import Window from './components/Window/Window';
import TitleWindow from './components/Window/TitleWindow/TitleWindow';
import ParticipantsWindow from './components/Window/ParticipantsWindow/ParticipantsWindow';
import ExpensesWindow from './components/Window/ExpensesWindow/ExpensesWindow';
import Debts from './components/Debts/Debts';



class App extends Component {

  state = this.initialState;

  get initialState() {
    return {
      positionY: 0,
      windows: ['start', 'title', 'names', 'expenses', 'debts'],
      index: 0,
      title: '',
      names: [],
      expenses: [
        { id: 0, name: '', cost: '', contributions: [], show: true, perPerson: 0, partExpValid: false }
      ],
      readyToChange: true,
      debts: []
      }
  }

  checkWindowsInputs = (windowIndex) => {
    switch(windowIndex) {
      case 1:
        return this.state.title !== '';
      case 2:
        return this.state.names.length > 0;
      case 3:
        return this.state.expenses.reduce((prev, cur) => {
          return this.checkExpenseValid(cur) && prev;
        }, this.checkExpenseValid(this.state.expenses[0]));
      default:
        return true;
    }
  }

  checkExpenseValid = (expense) => (expense.name !== '' && 
                                    expense.cost !== '' && 
                                    expense.contributions.filter(el => el === '').length === 0 && 
                                    expense.partExpValid) ? true : false;

  checkPartialExpenses = (expense) => expense.contributions
                                      .reduce((prev, cur) => Number(cur)+Number(prev)) === Number(expense.cost);

  nextButtonHandler = () => {
    let position = this.state.positionY;
    let windowIndex = this.state.index;
    if (this.checkWindowsInputs(windowIndex)) {
      position -= 100;
      windowIndex += 1;
      this.setState({positionY: position, index: windowIndex, readyToChange: true})
    } else {
      const expenses = [...this.state.expenses];
      expenses.forEach(expense => expense.show = !(expense.partExpValid && expense.contributions.filter(el => el === '').length === 0));
      this.setState({readyToChange: false});
    };
  }

  backButtonHandler = () => {
    const position = this.state.positionY+100;
    const index = this.state.index - 1;
    this.setState({positionY: position, index: index, readyToChange: true});
  }

  updateTitleHandler = (e) => {
    const title = e.target.value;
    this.setState({ title: title });
  }

  updateNamesHandler = (e) => {
    const names = e.target.value.split(',').map((name, index) => {
      return {
        id: index,
        name: name.trim(),
        debt: 0
      }
    });
    const newExpenses = this.addEmptyContributions([...this.state.expenses], names);
    this.setState({ names: names, expenses: newExpenses });
  }

  addEmptyContributions = (expenses, names) => {
    if (expenses[0].contributions.length === 0) {
      const contributions = names.map(name => '');
      expenses[0].contributions = contributions;
    } else {
      let i = names.length - expenses[0].contributions.length;
      for(i; i> 0; i--) {
        expenses.forEach(expense => expense.contributions.push(''))
      }
    }
    return expenses;
  }

  deleteNameHandler = (id) => {
    const names = [...this.state.names];
    const index = names.findIndex(name => name.id === id);
    names.splice(index, 1);
    const expenses = [...this.state.expenses];
    expenses.forEach(expense => {
      expense.contributions.splice(index, 1);
      expense.partExpValid = this.checkPartialExpenses(expense);
    });
    this.setState({ names: names, expenses: expenses });
  }

  addExpenseHandler = () => {
    const contributions = this.state.names.map(name => '');
    const expenses = [...this.state.expenses];
    expenses.forEach(expense => expense.show = false);
    const newId = expenses[expenses.length-1].id + 1;
    expenses.push({ id: newId, name: '', cost: '', contributions: contributions, show: true, perPerson: 0, valid: false });
    this.setState( { expenses: expenses });
  }

  toggleExpenseHandler = (id) => {
    const expenses = [...this.state.expenses];
    const index = expenses.findIndex(expense => expense.id === id);
    expenses[index].show = !expenses[index].show;
    this.setState( {expenses: expenses});
  }

  deleteExpenseHandler = (id) => {
    let expenses = [...this.state.expenses];
    const index = expenses.findIndex(expense => expense.id === id);
    if (expenses.length > 1) {
      expenses.splice(index, 1)
    } else {
      expenses[0] = { id: 0, name: '', cost: '', contributions: [], show: true, perPerson: 0, partExpValid: false };
    };
    this.setState({expenses: expenses});
  }

  updateNameHandler = (id, e) => {
    const name = e.target.value;
    let expenses = [...this.state.expenses];
    const index = expenses.findIndex(expense => expense.id === id);
    expenses[index].name = name;
    this.setState({ expenses: expenses });
  }

  updateCostHandler = (id, e) => {
    const cost = e.target.value;
    let expenses = [...this.state.expenses];
    const index = expenses.findIndex(expense => expense.id === id);
    expenses[index].cost = cost;
    this.setState({ expenses: expenses });
  }

  updateContributions = (expenseId, personId, e) => {
    const value = e.target.value;
    let expenses = [...this.state.expenses];
    const expenseIndex = expenses.findIndex(expense => expense.id === expenseId);
    expenses[expenseIndex].contributions[personId] = value;
    expenses[expenseIndex].partExpValid = this.checkPartialExpenses(expenses[expenseIndex]);
    this.setState({ expenses: expenses });
  }

  calculateExpPerPerson = () => {
    const expenses = [...this.state.expenses];
    expenses.forEach(expense => {
      const participants = expense.contributions.filter(el => el >= 0);
      expense.perPerson = expense.cost / participants.length;
    });
  }

  calculatePersonsDebts = () => {
    const names = [...this.state.names];
    names.forEach((person, index) => {
      person.debt = this.state.expenses.map(el => {
        return el.contributions[index] >= 0 ? el.perPerson - el.contributions[index] : 0;
      }).reduce((prev, cur) => prev + cur);
    });
  }

  createDebtsList = () => {
    const obligors = this.state.names.filter(name => name.debt.toFixed(2) > 0);
    let obligees = this.state.names.filter(name => name.debt.toFixed(2) < 0);
    const debts = [];
    obligors.forEach(obligor => {
      let i = 0;
      while (obligor.debt > 0 && i < obligees.length) {
        if (obligees[i].debt < 0) {
          if (obligor.debt <= -obligees[i].debt) {
            debts.push({obligorName: obligor.name, obligeeName: obligees[i].name, debt: obligor.debt});
            obligees[i].debt = obligees[i].debt + obligor.debt;
            obligor.debt = 0;
          } else {
            debts.push({obligorName: obligor.name, obligeeName: obligees[i].name, debt: -obligees[i].debt});
            obligor.debt = obligor.debt + obligees[i].debt;
            obligees[i].debt = 0;
          }
        }
        i++;
      }
    });
    this.setState({ debts: debts });
  }

  calculate = () => {
    
    this.calculateExpPerPerson();

    this.calculatePersonsDebts();

    this.createDebtsList();

    this.nextButtonHandler();
    
  }

  newCalculation = () => {
    this.setState(this.initialState);

  }

  render() {

    return (
      
      <Screen>
        <Layout position={this.state.positionY}>
          <StartScreen next={this.nextButtonHandler} />
          <Window>
            <TitleWindow
              title={this.state.title} 
              updateTitle={this.updateTitleHandler} 
              next={this.nextButtonHandler} 
              back={this.backButtonHandler}
              readyToChange={this.state.readyToChange} />
          </Window>
          <Window>
            <ParticipantsWindow
              names={this.state.names} 
              updateNames={this.updateNamesHandler} 
              nameClicked={this.deleteNameHandler}
              next={this.nextButtonHandler} 
              back={this.backButtonHandler}
              readyToChange={this.state.readyToChange} />
          </Window>
          <Window>
            <ExpensesWindow
              names={this.state.names}
              expenses={this.state.expenses} 
              updateName={this.updateNameHandler}
              updateCost={this.updateCostHandler}
              updateContributions={this.updateContributions}
              toggleExpense={this.toggleExpenseHandler}
              deleteExpense={this.deleteExpenseHandler}
              nextExpense={this.addExpenseHandler}
              next={this.nextButtonHandler} 
              back={this.backButtonHandler}
              calculate={this.calculate}
              readyToChange={this.state.readyToChange} />
          </Window>
          <Window>
            <Debts
              debts={this.state.debts}
              title={this.state.title}
              back={this.backButtonHandler}
              newCalc={this.newCalculation} />
          </Window>   
        </Layout>
      </Screen>
        
    );
  }
}

export default App;
